@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;700&family=Roboto:wght@100;400;700;900&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}

html,body{
    height: 100%;
}

body{
    background-color: #c7c7c7;
}

.container{
    background-color: #fff;
    width: 100%;
    max-width: 900px;
    padding: 50px 90px;
    border-radius: 10px;
    box-shadow: 0 1px 5px #ccc;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}