h1{
    text-align: center;
    margin-bottom: 30px;
}

form{
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
}

input{
    width: 100%;
    height: 45px;
    margin-bottom: 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding-left: 8px;
    font-size: 16px;
    outline: 0;
}

input[type=date]{
    margin-top: 5px;
}

button{
    width: 100%;
    height: 45px;
    text-align: center;
    align-items: center;
    color: #fff;
    font-size: 16px;
    border-radius: 5px;
    border: 0;
    background-color: #3d3d3d;
    margin-top: 20px;
    cursor: pointer;
    transition: .2s;
}

button:hover{
    background-color: #313131;
}